/* Home css */
.home {
    position:relative;
    display:flex;
    flex-direction:column;
    height:3340px;
}
/* //Home css */

/* Header css */
.header {
    position:fixed;
    display:flex;
    justify-content:center;
    align-items:center;
    width:100%;
    height:56px;
    background:#212529;
    z-index:5;
}

.headerWrap {
    display:flex;
    align-items:center;
    width:99%;
    height:40px;
}

.headerName {
    display:flex;
    justify-content:center;
    align-items:center;
    color:white;
    font-weight:bold;
    height:100%;
}

.headerSubWrap {
    margin-left:20px;
    display:flex;
    align-items:center;
    justify-content:space-between;
    width:100%;
}

.headerSubWrap nav ul {
    width:300px;
    height:100%;
}

.headerSubWrap nav ul {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
}

.headerSubWrap nav ul li {
    color:#FFFFFF8C;
    transition:0.2s;
}

.headerSubWrap nav ul li:hover {
    cursor:pointer;
    color: #dee2e6;
}

.headerRightWrap {
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:390px;
}

.headerLoginBtn {
    width:70px;
    height:30px;
    font-size:16px;
    color:white;
    background:#6B6B6B;
    border-color:white;
    box-sizing:border-box;
}

.headerLoginBtn:hover {
    cursor:pointer;
}

.headerSearchWrap {
    position:relative;
    width:210px;
    height:40px;
    margin-left:10px;
}

.headerSearchWrap input {
    position:absolute;
    width:100%;
    height:100%;
    font-size:17px;
    border:none;
    outline:none;
    background:none;
    color:#adb5bd;
    padding:0 25px 0 10px;
}

.headerSearchWrap label {
    display:block;
    width:100%;
    height:100%;
    border:1px solid #6B6B6B;
    border-radius:5px;
    box-sizing:border-box;
    transition:0.2s;
}

.headerSearchEX {
    position:absolute;
    display:none;
    top:30%;
    right:10px;
    color:white;
}

.headerSearchBtn {
    width:80px;
    height:40px;
    border-radius:5px;
    background:none;
    color:#198754;
    border:1px solid #198754;
    box-sizing:border-box;
    font-size:17px;
    line-height:17px;
    transition:0.2s;
}

.headerSearchBtn:hover {
    cursor:pointer;
    background:#198754;
    color:white;
}

    /* input EX remove */
input::-ms-clear,
input::-ms-reveal {
    display:none;
    width:0;
    height:0;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
    display:none;
}
    /* //input EX remove */
/* //Header css */

/* Banner css */
.banner {
    position:relative;
    overflow: hidden;
}

.bannerArrowWrap {
    position:absolute;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    align-items:center;
    left:50%;
    top:55%;
    transform:translate(-50%, -10%);
    width:85vw;
    z-index:2;
    font-size:40px;
    color:#FFFFFF8C;
}

.bannerArrow {
    display:flex;
    width:100%;
    justify-content:space-between;
    align-items:center;
}

.bannerArrowIcon {
    transition:0.2s;
    user-select:none;
    
}

.bannerArrowIcon:hover {
    cursor:pointer;
    color:white;
}

.bannerArrowBtn {
    position:absolute;
    display:flex;
    justify-content:center;
    width:150px;
    height:40px;
    left:50%;
    bottom:0%;
    transform:translate(-50%, -10%);
    user-select:none;
    z-index:2;
}

.bannerArrowBtnIcon {
    width:40px;
    height:40px;
    color:#FFFFFF8C;
}

.bannerArrowBtnIcon:hover {
    cursor:pointer;
}

.bannerArrowBtnIcon:first-child {
    color:white;
}

.bannerWrap {
    position:relative;
    display:flex;
    flex-direction:row;
    width:500vw;
    transform:translateX(-100vw);
    height:700px;
    user-select:none;
    z-index:1;
}

.bannerTxtWrap {
    display:flex;
    justify-content:center;
    align-items:center;
    width:100vw;
    height:100%;
    background-size:100% 100%;
    background-repeat:no-repeat;
}

.bannerTxtSubWrap {
    display:flex;
    flex-direction:column;
    margin-top:100px;
    width:70vw;
    color:white;
}

.bannerTxtWrap:nth-child(3) .bannerTxtSubWrap {
    align-items:center;
}

.bannerTxtWrap:nth-child(1) .bannerTxtSubWrap,
.bannerTxtWrap:nth-child(4) .bannerTxtSubWrap {
    align-items:end;
}

.bannerTxtSubWrap h4 {
    font-size:37px;
    font-weight:500;
    margin-bottom:10px;
}

.bannerTxtSubWrap span {
    display:block;
    font-size:18px;
    margin-bottom:20px;
}

.bannerTxtWrap:nth-child(2) .bannerTxtSubWrap span,
.bannerTxtWrap:nth-child(5) .bannerTxtSubWrap span {
    font-weight:bold;
}

.bannerTxtWrap:nth-child(1) .bannerTxtSubWrap div,
.bannerTxtWrap:nth-child(4) .bannerTxtSubWrap div {
    display:flex;
}

.bannerTxtBtn {
    display:flex;
    justify-content:center;
    align-items:center;
    height:50px;
    font-size:21px;
    line-height:21px;
    border-radius:10px;
    color:white;
    text-align:center;
    background:#0d6efd;
    transition:0.2s;
}

.bannerTxtBtn:hover {
    cursor:pointer;
    background:#0b5ed7;
}

.bannerTxtWrap:nth-child(2) .bannerTxtBtn,
.bannerTxtWrap:nth-child(5) .bannerTxtBtn {
    width:110px;
}

.bannerTxtWrap:nth-child(3) .bannerTxtBtn {
    width:140px;
}

.bannerTxtWrap:nth-child(1) .bannerTxtBtn,
.bannerTxtWrap:nth-child(4) .bannerTxtBtn {
    width:165px;
}
/* //Banner css */

/* Section css */
.section {
    height:2550px;
    margin-bottom:70px;
}

.sectionWrap {
    width:66vw;
    height:100%;
    margin:auto auto;
}
    /* firstSection css */
.firstSection {
    margin-top:70px;
    height:450px;
    border-bottom:2px solid #e9ecef;
    box-sizing:border-box;
}

.firstSectionWrap {
    display:flex;
    flex-direction:row;
    justify-content:space-between;
}

.firstSectionSubWrap {
    display:flex;
    flex-direction:column;
    align-items:center;
    text-align:center;
    width:22vw;
}

.firstSectionSubWrap img {
    border-radius:50%;
    width:140px;
    height:140px;
}

.firstSectionSubWrap p {
    font-size:30px;
}

.firstSectionSubWrap span {
    display:block;
    width:380px;
    margin:5px 0 20px 0;
}

.firstSectionSubWrap p,
.firstSectionSubWrap span {
    color:#212529;
}

.firstSectionLink {
    display:flex;
    justify-content:center;
    align-items:center;
    width:130px;
    height:38px;
    font-size:17px;
    color:white;
    background:#6c757d;
    border-radius:7px;
    border:none;
    transition:0.2s;
}

.firstSectionLink:hover {
    background:#5c636a;
}

.firstSectionIcon {
    margin:3px 0 0 5px;
    width:10px;
    height:10px;
}
    /* //firstSection css */

    /* sameSection css */
.sameSection {
    display:flex;
    align-items:center;
    width:100%;
    height:700px;
    border-bottom:2px solid #e9ecef;
    box-sizing:border-box;
}

.sameSectionWrap {
    display:flex;
    justify-content:space-between;
    width:100%;
    height:500px;
}

.sameSectionWrap div {
    width:735px;
    align-self:center;
    display:flex;
    flex-direction:column;
    justify-content:center;
}

.sameSection:nth-child(3) .sameSectionWrap div {
    order:2;
}

.sameSectionWrap p {
    font-size:46px;
    color:#212529;
}

.sameSectionWrap span {
    white-space:pre-wrap;
    font-size:20.5px;
    line-height:32px;
    font-weight:300;
}

.sameSectionWrap img {
    width:500px;
    height:100%;
}
    /* //sameSection css */
/* //Section css */

/* Footer css */
.footer {
    display:flex;
    height:200px;
}

.footerWrap {
    display:flex;
    justify-content:space-between;
    width:66vw;
    margin:auto auto;
}

.footerLeft {
    display:flex;
    justify-content:space-between;
    align-items:center;
    width:325px;
}

.footerLeft span {
    margin:0 5px 0 5px;
}

.footerLeft div:nth-child(2) span {
    margin-left:0;
}

.footerLink,
.footerWrap a {
    color:rgb(13, 110, 253);
}
/* //Footer css */

/* Dynamic css */
@media (max-width:1600px) {
    .firstSectionSubWrap span {
        white-space:nowrap;
        text-overflow:ellipsis;
        overflow:hidden;
        width:250px;
    }
}

@media (max-width:1200px) {
    .home {
        height:4100px;
    }

    .bannerWrap {
        height:500px;
    }

    .bannerTxtSubWrap {
        margin-top:200px;
    }

    .section {
        height:3330px;
    }

    .firstSection {
        height:950px;
    }

    .firstSectionWrap {
        flex-direction:column;
        justify-content:space-between;
        align-items:center;
        height:900px;
    }

    .firstSectionSubWrap span {
        white-space:pre-wrap;
        width:380px;
    }

    .sameSection {
        height:800px;
    }

    .sameSectionWrap {
        flex-direction:column;
        justify-content:center;
        align-items:center;
        text-align:center;
        height:600px;
    }

    .sameSectionWrap div {
        width:100%;
    }
}

@media (max-width:800px) {
    .headerRightWrap {
        display:none;
    }
}

@media (max-width:700px) {
    .sameSectionWrap span {
        white-space:nowrap;
        text-overflow:ellipsis;
        overflow:hidden;
    }
}

@media (max-width:650px) {
    .bannerTxtSubWrap span {
        font-size:15px;
    }

    .footerWrap {
        flex-direction:column;
        align-items:center;
        width:100%;
    }
}